.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1, h2, h3 {
  color: #333;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #8A2BE2;
}

h2 {
  border-bottom: 2px solid #8A2BE2;
  padding-bottom: 5px;
  margin-top: 30px;
  color: #54008b;
}

h3 {
  margin-top: 20px;
  color: #6a0dad;
}

p {
  margin-bottom: 15px;
  line-height: 1.6;
}

ul, ol {
  margin-bottom: 15px;
  padding-left: 20px;
}

li {
  margin-bottom: 5px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
}

th {
  background-color: #8A2BE2;
  color: #fff;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

.blog-image {
  width: 300px;
  margin-left: 20px;
  max-width: 40%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.intro-flex-container {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.intro-text {
  flex: 1;
  margin-right: 20px;
}

section {
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .container {
    width: 95%;
    padding: 10px;
  }

  .intro-flex-container {
    flex-direction: column;
  }

  .blog-image {
    width: 100%;
    max-width: 100%;
    margin: 20px 0;
  }

  .intro-text {
    margin-right: 0;
  }
} 